let SlidersArray = []
function sliderGallery() {
    $(".gallery").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.gallery[data-slider-id="${index}"] .swiper`, {
            slidesPerView: "auto",
            spaceBetween: 10,
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            }, 
        })
    })
}

$(document).ready(function () {
    sliderGallery();

    SlidersArray.forEach((swiperInstance, index) => {
    swiperInstance.on('slideChange', () => {
        if (typeof Fancybox !== 'undefined') {
            Fancybox.update();
        }
    });
});
  
    //Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    $(".header-menu .header__list li").click((function() {
        $(".burger").removeClass("burger--active"),
        $(".header-menu").removeClass("header-menu--active"),
        $("body").removeClass("fixed-body")
    }
    )),

    //Select
    $(function() {
        $(".select-hidden .select-item").click(function(e) {
            e.stopPropagation();
            let $item = $(this);
            let html = $item.html();
            $item.closest(".select").find(".select-current").html(html);
            $item.closest(".select").removeClass("select--active");
            $item.siblings(".select-item").removeClass("select-item--active");
            $item.addClass("select-item--active");
        });

        $(".select-current").on("click", function(e) {
            e.stopPropagation();
            let $sel = $(this).closest(".select");
            $(".select").not($sel).removeClass("select--active");
            $sel.toggleClass("select--active");
        });

        $(".select").on("mouseleave", function() {
            $(this).removeClass("select--active");
        });

        $(document).on("click", function(e) {
            if (!$(e.target).closest(".select").length) {
                $(".select").removeClass("select--active");
            }
        });

        var mq = window.matchMedia('(max-width: 992px)');

        function bindSelectShow() {
            $('.select-show').off('.selectShow');

            $(".select").removeClass("select--active");

            if (mq.matches) {
                $('.select-show').on('click.selectShow', function(e) {
                    e.stopPropagation();
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.toggleClass("select--active");
                });
            } else {
                $('.select-show').on('mouseenter.selectShow', function(e) {
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.addClass("select--active");
                });
            }
        }

        bindSelectShow();
        if (mq.addEventListener) {
            mq.addEventListener('change', bindSelectShow);
        } else if (mq.addListener) { 
            mq.addListener(bindSelectShow);
        }
    });

    //Calendar
    new AirDatepicker('#range', {
        range: true,
        multipleDatesSeparator: ' - ',
        inline: true,
    });
     const $calendar = $('.calendar');
    function addActive() {
        $calendar.addClass('calendar--active');
    }
    function removeActive() {
        $calendar.removeClass('calendar--active');
    }
    function handleClickOutside(e) {
        if (!$calendar.is(e.target) && $calendar.has(e.target).length === 0) {
            removeActive();
        }
    }
    const isDesktop = window.matchMedia('(min-width: 993px)');
    function handleMediaChange() {
        $calendar.off('mouseenter mouseleave click');
        $(document).off('click', handleClickOutside);

        if (isDesktop.matches) {
            $calendar.hover(addActive, removeActive);
        } else {
            $calendar.on('click', addActive);
            $(document).on('click', handleClickOutside);
        }
    }
    isDesktop.addEventListener('change', handleMediaChange);
    handleMediaChange();

    //Accordion
    if (window.innerWidth >= 993) {
        $(".item-show").click(function () {
            let parent = $(this).parents(".item")
            if($(parent).hasClass("item--active")){
                $(".item").removeClass("item--active")
                $(".item-hidden").slideUp()
            }else{
                $(".item").removeClass("item--active")
                $(parent).addClass("item--active")
                $(".item-hidden").slideUp()
                $(this).siblings(".item-hidden").slideDown()
            }
        })
    }
    
    //Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    $(".tab2").click(function () {
        let path = $(this).attr("data-tab-path2")
        $(this).siblings(".tab2").removeClass("tab2--active")
        $(this).addClass("tab2--active")

        $(this).parents(".tabs-inner").find(".tab2__content").removeClass("tab2__content--active")
        $(this).parents(".tabs-inner").find(`.tab2__content[data-content-path2="${path}"]`).addClass("tab2__content--active")
    })

    $(".tab3").click(function () {
        let path = $(this).attr("data-tab-path3")
        $(this).siblings(".tab3").removeClass("tab3--active")
        $(this).addClass("tab3--active")

        $(this).parents(".tabs-inner").find(".tab3__content").removeClass("tab3__content--active")
        $(this).parents(".tabs-inner").find(`.tab3__content[data-content-path3="${path}"]`).addClass("tab3__content--active")
    })

    //Regions
    $(function () {
        $(document).on('mouseenter', '.region', function () {
            var $this = $(this);
            $this.addClass('region--hover');

            $this.siblings('.region').each(function () {
            var $sib = $(this);
            if (!$sib.hasClass('region--active')) {
                $sib.addClass('region--disabled');
            }
            });
        });

        $(document).on('mouseleave', '.region', function () {
            var $this = $(this);
            $this.removeClass('region--hover');

            var $parent = $this.parent();
            if ($parent.children('.region.region--active').length === 0) {
            $parent.children('.region.region--disabled').removeClass('region--disabled');
            }
        });

        $(document).on('click', '.region', function (e) {
            e.preventDefault();
            var $this = $(this);
            var $parent = $this.parent();

            $parent.children('.region').each(function () {
            var $sib = $(this);
            if (this !== $this[0]) {
                $sib.removeClass('region--active region--hover').addClass('region--disabled');
            }
            });

            $this.addClass('region--active').removeClass('region--disabled region--hover');
        });

        $(document).on('click', function (e) {
            if ($(e.target).closest('.region').length === 0) {
            $('.region').removeClass('region--disabled region--active');
            }
        });
    });

    //Show-all
    $('.show-all').click(function () {
        $(this).toggleClass('show-all--active')
        $(this).siblings('.all-cards').toggleClass('all-cards--active')
    })

    //Inputs
    $('.form-group input').each(function() {
        var input = $(this); 
        var title = input.prev(); 
        
        if (title.hasClass('input-title')) {
            input.on('focus', function() {
                title.addClass('input-title--active');
            });
            
            input.on('blur', function() {
                if (input.val().trim() === '') {
                    title.removeClass('input-title--active');
                }
            });
            
            if (input.val().trim() !== '') {
                title.addClass('input-title--active');
            }
        }
    });

    Inputmask({
        mask: "+7 (999) 999-99-99",
        clearIncomplete: true,
        showMaskOnHover: false
    }).mask(".phone");

    Inputmask({ alias: "email" }).mask(".email");

    //Cookies
    $('.cookies__btn').click(function () {
        $(this).parents('.cookies').css("display","none")
    })

    //Modal
    let modalStack = [];

    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal");
        openModal(modalId);
    });

    function openModal(modalId) {
        $(`#${modalId}`).addClass("modal--active");
        
        modalStack.push(modalId);
        
        if (modalStack.length === 1) {
            $("body").addClass("fixed-body");
        }
    }

    function closeModal() {
        if (modalStack.length > 0) {
            let lastModal = modalStack.pop();
            
            $(`#${lastModal}`).removeClass("modal--active");
            
            $(`#${lastModal} video`).each(function () {
                this.pause();
                this.currentTime = 0;
            });
            
            if (modalStack.length === 0) {
                $("body").removeClass("fixed-body");
            }
        }
    }

    $(document).on('click', '.modal', function (e) {
        if ($(e.target).hasClass('modal') && modalStack.length > 0) {
            closeModal();
        }
    });

    $(document).on('click', '._close-modal', function () {
        let modal = $(this).closest('.modal');
        let modalId = modal.attr('id');
        
        if (modalStack[modalStack.length - 1] === modalId) {
            closeModal();
        }
    });

    $(document).on('click', '.modal__dialog', function (e) {
        e.stopPropagation();
    });


})

//Form
document.addEventListener('DOMContentLoaded', function() {
    const form = document.querySelector('.form');
    
    function updateInputTitle(fieldElement) {
        const container = fieldElement.closest('.form-group') || fieldElement.closest('.select');
        if (container) {
            const inputTitle = container.querySelector('.input-title');
            if (inputTitle) {
                let isActive = false;
                if (fieldElement.tagName === 'INPUT') {
                    isActive = fieldElement.value.trim() !== '';
                } else if (fieldElement.classList.contains('select')) {
                    const currentText = fieldElement.querySelector('.select-current').textContent.trim();
                    isActive = currentText !== 'Выберите здравницу' && currentText !== 'Выберите программу отдыха';
                }
                if (isActive) {
                    inputTitle.classList.add('input-title--active');
                } else {
                    inputTitle.classList.remove('input-title--active');
                }
            }
        }
    }
    
    function addActiveOnError(container) {
        const inputTitle = container.querySelector('.input-title');
        if (inputTitle) {
            inputTitle.classList.add('input-title--active');
        }
    }
    
    function removeError(container) {
        const errorElement = container.querySelector('.error');
        if (errorElement) {
            errorElement.classList.remove('error--active');
        }
        const selectCurrent = container.querySelector('.select-current');
        if (selectCurrent) {
            selectCurrent.classList.remove('select-current--hidden');
        }
    }
    
    const inputs = form.querySelectorAll('input');
    const selects = form.querySelectorAll('.select');
    
    inputs.forEach(function(input) {
        updateInputTitle(input); 
        input.addEventListener('input', function() {
            updateInputTitle(input);
            removeError(input.closest('.form-group')); 
        });
        input.addEventListener('focus', function() {
            const container = input.closest('.form-group');
            if (container) {
                const inputTitle = container.querySelector('.input-title');
                if (inputTitle) inputTitle.classList.add('input-title--active');
                removeError(container);
            }
        });
        input.addEventListener('blur', function() {
            updateInputTitle(input);
        });
    });
    
    selects.forEach(function(select) {
        updateInputTitle(select); 
        select.addEventListener('click', function() {
            const inputTitle = select.querySelector('.input-title');
            if (inputTitle) inputTitle.classList.add('input-title--active');
            removeError(select); 
        });
        const selectShow = select.querySelector('.select-show');
        if (selectShow) {
            selectShow.addEventListener('mouseenter', function() {
                removeError(select); 
            });
        }
    });
    
    form.addEventListener('submit', function(event) {
        let hasErrors = false;
        
        const selectsWithError = form.querySelectorAll('.select .error');
        selectsWithError.forEach(function(errorElement) {
            const select = errorElement.closest('.select');
            const currentText = select.querySelector('.select-current').textContent.trim();
            const isDefault = currentText === 'Выберите здравницу' || currentText === 'Выберите программу отдыха';
            if (isDefault) {
                errorElement.classList.add('error--active');
                addActiveOnError(select); 
                const selectCurrent = select.querySelector('.select-current');
                if (selectCurrent) selectCurrent.classList.add('select-current--hidden');
                hasErrors = true;
            } else {
                errorElement.classList.remove('error--active');
                const selectCurrent = select.querySelector('.select-current');
                if (selectCurrent) selectCurrent.classList.remove('select-current--hidden');
            }
        });
        
        const inputsWithError = form.querySelectorAll('.form-group .error');
        inputsWithError.forEach(function(errorElement) {
            const input = errorElement.previousElementSibling;
            if (input && input.tagName === 'INPUT' && input.value.trim() === '') {
                errorElement.classList.add('error--active');
                const container = input.closest('.form-group');
                addActiveOnError(container);
                hasErrors = true;
            } else {
                errorElement.classList.remove('error--active');
            }
        });
        
        if (hasErrors) {
            event.preventDefault();
        }
    });
});
